.listItem {
    color: white;
    width: 300px;
    height: 40px;
    margin: 20px auto;
    border-radius: 10px;
    background-color: rgba(223, 236, 36, 0.3s);
    display: flex;
}

.listItem input {
    padding: 0;
    margin: 5px;
    justify-content: center;
    flex-grow: 1;
    background-color: transparent;
    border: 0;
    outline: 0;
    font-size: 16px;
    color: whitesmoke;
    font-weight: bolder;
}

.listItem input:focus{
    outline: none;
}

.actions {
    margin-top: 8px;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
}

.actions #trash-icon{
    margin: 0px 10px;
}

#no-task-info{
    color: rgba(255, 255, 30, 0.8s);
    text-align: center;
}