#to-do-form input{
    background-color:rgba(255,255,255,0.4);
    border: 0;
    width: 200px;
    height: 50px;
    padding: 0 20px;
    margin: 20px;
    font-size: 18px;
    border-radius: 10px;
    color: #fff;
}

#to-do-form input::placeholder{
    color: #fff;
    font-weight: bolder;
}

#to-do-form input:focus, #to-do-form button:focus{
    outline: none;
}

#to-do-form button{
    background-color: goldenrod;
    height: 40px;
    font-size: 16px;
    border: 0;
    border-radius: 5px;
    color: rgba(0, 25, 31, 0.9);
    font-weight: bold;
    cursor: pointer;
}